$rowSize: 5em;
$gridGap: 1em;
.App {
    text-align: center;
}

.wrapper {
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    background-color: #fff;
    color: #000;
    margin: 0 auto;
}

.box-container {
    width: 100%;
    padding: $gridGap;
    box-sizing: border-box;

    &--1 {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, $rowSize);
        gap: $gridGap;
        padding-bottom: $gridGap/2;

        .box-1 {
            grid-column: span 2;
            background-color: #2c88d9;
        }

        .box-2 {
            grid-column: span 2;
            order: 1;
            background-color: #897a5f;
        }

        .box-3 {
            background-color: #ac6363;
        }

        .box-4 {
            background-color: #d3455b;
        }
    }

    &--2 {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: $rowSize;
        gap: $gridGap;
        padding-top: $gridGap/2;

        .box-5 {
            order: 1;
            background-color: #730fc3;
        }

        .box-6 {
            order: 3;
            background-color: #1aae9f;
        }

        .box-7 {
            grid-column: span 2;
            order: 0;
            background-color: #207868;
        }

        .box-8 {
            order: 2;
            background-color: #6558f5;
        }

        .box-9 {
            order: 4;
            background-color: #4b5c6b;
        }
    }
}

@media all and (min-width: 600px) {
    $rowSize: 11em;

    .box-container {
        &--1 {
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: repeat(2, $rowSize);
            column-gap: $gridGap * 1.5;

            .box-1 {
                grid-column: span 2;
                grid-row: span 2;
            }

            .box-2 {
                order: 0;
                grid-column: span 2;
            }
        }

        &--2 {
            grid-template-columns: 1fr 1.5fr 1fr;
            grid-template-rows: repeat(2, $rowSize);

            .box-5 {
                order: 0;
            }

            .box-6 {
                order: 3;
            }

            .box-7 {
                order: 1;
                grid-row: span 2;
                grid-column: auto;
            }

            .box-8 {
                order: 2;
            }

            .box-9 {
                order: 4;
            }
        }
    }
}
