.box {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 150ms ease-out, transform 150ms ease-out;

    &:hover {
        transform: translateY(-3px);
    }
}
